import React, { Component } from "react"
import numeral from "numeral"
import Fade from "react-reveal/Fade"
import styled, { css } from "styled-components"
import FillIcon from "../components/FillIcon"
import {
  TERTIARY_TEXT,
  SECONDARY_TEXT,
  PRIMARY_TEXT,
  SMALLCHAT_SHADOW,
  SMALLCHAT_PURPLE
} from "../components/consts/colors"
import AddtoSlack from "../components/AddtoSlack"
import LogoMarquee from "../components/LogoMarquee"
import SectionBackground from "../components/SectionBackground"
import pricingImage from "../assets/svgs/PricingBackground.svg"
import SectionCalloutBackground from "../assets/svgs/SectionCalloutBackground.svg"

import { SMALLCHAT_BKG } from "../components/consts/colors"
import SectionCallout from "../components/SectionCallout"

const WhatIsHeader = styled.header`
  z-index: 2;
  text-align: center;
  padding: 0 50px;
  width: 600px;
  margin: 0 auto;
  margin-bottom: 50px;
  h1 {
    display: inline-block;
    margin-bottom: 20px;
  }
`

const MessageContainer = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 150px;
  p {
    font-size: 24px;
    line-height: 30px;
  }
  a {
    font-size: 22px;
  }
`
const MessageSent = styled.div`
  margin: 30px;
  padding: 20px;
  position: relative;
  background-color: white;
  box-shadow: 0px 8px 20px ${SMALLCHAT_SHADOW};
  border-radius: 12px;
  width: 380px;
  left: -12%;
`

const MessageReceived = styled.div`
  margin: 30px;
  padding: 20px;
  position: relative;
  background-color: ${SMALLCHAT_BKG};
  box-shadow: 0px 8px 20px ${SMALLCHAT_SHADOW};
  border-radius: 12px;
  width: 380px;
  left: 12%;
`
const MessageIcon = styled.div`
  position: relative;
  top: 15px;
  left: -35px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  margin: 10px;
  border-radius: 50px;
  box-shadow: 0px 3px 10px ${SMALLCHAT_SHADOW};
  svg {
    width: 30px;
    height: 30px;
  }
`
const MessageBlock = styled.div`
  display: flex;
  justify-content: flex-start;
`
export default class WhatIs extends Component {
  state = {
    interval: 1,
    activeIndex: null
  }

  selectPlan = index => {
    if (index !== this.state.activeIndex) {
      this.setState({ activeIndex: index })
    }
  }

  setInterval = interval => {
    this.setState({ interval })
  }

  render() {
    return (
      <div>
        <WhatIsHeader>
          <h1>Turn curiosity into connections.</h1>
          <p>
            Smallchat lets you quickly and easily engage visitors on your
            website, and convert those visitors into customers.
          </p>
        </WhatIsHeader>
        <MessageContainer>
          <Fade right delay={0} duration={400}>
            <MessageReceived>
              <p>
                 What if a site visitor had asimple way to talk directly to
                a person on the sites team?
              </p>
            </MessageReceived>
          </Fade>

          <Fade left delay={500} duration={400}>
            <MessageBlock>
              <MessageIcon>
                <FillIcon name="smiley" />
              </MessageIcon>
              <MessageSent>
                <p>
                   Yeah, its called Smallchat! It connects a user on your
                  site to freely message a member of your team on Slack.
                </p>
              </MessageSent>
            </MessageBlock>
          </Fade>

          <Fade right delay={750} duration={200}>
            <MessageReceived>
              <p>
                Wow, actual humans talking to each other . Thats probably
                good for business.
              </p>
            </MessageReceived>
          </Fade>

          <Fade left delay={500} duration={200}>
            <MessageBlock>
              <MessageIcon>
                <FillIcon name="smiley" />
              </MessageIcon>
              <MessageSent>
                <p>
                  No kidding. Smallchat is more effective at leading people all
                  the way through transactions , helping customers choose
                  the best product for them , and making sure all their
                  questions are answered .
                </p>
              </MessageSent>
            </MessageBlock>
          </Fade>

          <Fade right delay={250} duration={200}>
            <MessageReceived>
              <p>so how does that work? </p>
            </MessageReceived>
          </Fade>

          <Fade left delay={500} duration={200}>
            <MessageBlock>
              <MessageIcon>
                <FillIcon name="smiley" />
              </MessageIcon>
              <MessageSent>
                <p>
                  Smallchat is a web chat integration for slack . It enables
                  you to talk to your sites visitors in real time through a
                  single channel on your teams slack account.
                </p>
              </MessageSent>
            </MessageBlock>
          </Fade>

          <Fade left delay={500} duration={200}>
            <MessageBlock>
              <MessageIcon>
                <FillIcon name="smiley" />
              </MessageIcon>
              <MessageSent>
                <p>
                  No need for additional chat software, use the tools youre
                  already familiar with 
                </p>
              </MessageSent>
            </MessageBlock>
          </Fade>

          <Fade right delay={250} duration={200}>
            <MessageReceived>
              <p>Sounds pretty valuable. Buthow much does it cost? </p>
            </MessageReceived>
          </Fade>

          <Fade left delay={500} duration={200}>
            <MessageBlock>
              <MessageIcon>
                <FillIcon name="smiley" />
              </MessageIcon>
              <MessageSent>
                <p>
                  Actually, you can use Smallchat for free-ninety-five!
                  Thatsright, it doesnt cost anything to get started. 
                </p>
              </MessageSent>
            </MessageBlock>
          </Fade>

          <Fade left delay={750} duration={200}>
            <MessageBlock>
              <MessageIcon>
                <FillIcon name="smiley" />
              </MessageIcon>
              <MessageSent>
                <p>
                  When youre ready, join one ofour Growth or Professional
                  tiers with even more powerful features  .
                </p>
              </MessageSent>
            </MessageBlock>
          </Fade>

          <Fade right delay={500} duration={200}>
            <MessageReceived>
              <p>Lets do this!  How I Join??</p>
            </MessageReceived>
          </Fade>

          <Fade left delay={750} duration={200}>
            <MessageBlock>
              <MessageIcon>
                <FillIcon name="smiley" />
              </MessageIcon>
              <MessageSent>
                <p>We have one of the fastest on-boardings ever simply</p>
                <a href="#">Add to Slack.</a>
              </MessageSent>
            </MessageBlock>
          </Fade>
        </MessageContainer>
        <SectionCallout
          darkShadow
          background={SectionCalloutBackground}
          backgroundSize="cover"
          backgroundPosition="80% 80%"
          title="Start your conversations today."
        />
      </div>
    )
  }
}



